    },
    handleRemove(file) {
      // const uid = file.uid
      // const objKeyArr = Object.keys(this.listObj)
      // for (let i = 0, len = objKeyArr.length; i < len; i++) {
      //   if (this.listObj[objKeyArr[i]].uid === uid) {
      //     delete this.listObj[objKeyArr[i]]
      //     return
      //   }
      // }
    },
    beforeUpload(file) {
      // const _self = this
      // const _URL = window.URL || window.webkitURL
      // const fileName = file.uid
      // this.listObj[fileName] = {}
      // return new Promise((resolve, reject) => {
      //   const img = new Image()
      //   img.src = _URL.createObjectURL(file)
      //   img.onload = function() {
      //     _self.listObj[fileName] = { hasSuccess: false, uid: file.uid, width: this.width, height: this.height }
      //   }
      //   resolve(true)
      // })
    },
    // 自定义覆盖图片上传方法
    async uploadSectionFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!')
        return
      }
      const formData = await this.$refs.imageCompressorCmp._imageCompressor(params.file)
      await this._upload(formData)
    },

    // --------------------------------------------------------------------------------------
    // 上传图片
    async _upload (formData) {
      const res = await this.$http.post(this.$api.uploadFile, formData)
      this.fileList.push({ url: res.data.data.webUrl })
    },
  }
