import Config from '@/config/config.js'

const MIXINS_DESCRIBES_APPEND_H5 = {
  methods: {
    // 视频上传
    beforeUploadVideo(file) {
      console.log('file', file)
      const isLt20M = file.file.size / 1024 / 1024 < 2000
      if (!isLt20M) {
        this.$message.error('上传视频大小不能超过2000MB哦!')
        return false
      }
      const formData = new FormData()
      formData.append('file', file.file)
      this._uploa(formData)
    },
    // 点击添加规格项目
    onAddSpec () {
      for (let item of this.attributeItem) {
        if (item.keyName === '' || item.keyValue.length === 0) {
          return this.$message.error('先把上一项规格填写完整')
        }
      }
      this.attributeItem.push({
        keyName: '',
        inputKeyName: '',
        keyValue: [],
        inputValueNum: '',
        inputKeyValue: '',
        status: true
      })
    },
    // 规格属性
    onAddSpecTag(index) {
      if (this.attributeItem[index].keyName === '') {
        return this.$message.error('请先确定规格名称')
      }
      if (this.attributeItem[index].inputKeyValue === '') {
        return this.$message.error('不能添加空规格')
      }
      if (this.attributeItem[index].keyValue.indexOf(this.attributeItem[index].inputKeyValue) !== -1) {
        return this.$message.error('规格不能重复')
      }
      this.saveInventoryIndex = index
      this.onSave()
      // this.dialogValueNum = true
    },
    // 自定义覆盖属性图片上传方法
    async onUploadSectionFile (params, scope) {
      let formData = await this._uploadSectionFile(params)
      console.log(11111111, formData)
      if (formData) {
        this._upload(formData)
          .then((res) => {
            scope.row.imgUrl = res
          })
      }
    },
    // 自定义覆盖主图图片上传方法
    async uploadSectionFile (params) {
      let formData = await this._uploadSectionFile(params)
      if (formData) {
        this._upload(formData)
          .then((res) => {
            this.model.imageUrl = res
          })
      }
    },

    // 私有方法---------------------------------------------------------------------------------------
    // 在富文本编辑前面添加h5页面的url
    _mixinsDescribesAppendH5 (describes) {
      describes = {
        goodsDetailsUrl: Config.wohoH5Url + '/goods_content.html',
        goodsDetailsContent: describes
      }
      return JSON.stringify(describes)
    },
    // 笛卡尔积算法
    _calcDescartes (array) {
      if (array.length < 2) return array[0].map((item) => [item]) || []
      return [].reduce.call(array, function (col, set) {
        var res = []
        col.forEach(function (c) {
          set.forEach(function (s) {
            var t = [].concat(Array.isArray(c) ? c : [c])
            t.push(s)
            res.push(t)
          })
        })
        return res
      })
    },
    // 转化成表格的数据渲染到表格中去
    _attributeItemTable () {
      try {
        this.attributeItemTable = this._calcDescartes(this.attributeItem.map((item) => {
          return item.keyValue.map((keyValueItem) => {
            return keyValueItem.valueName
          })
        }))
      } catch (error) {
        console.error('手动抛出全部删除异常处理', error)
      }
      this.attributeItemTable = this.attributeItemTable.map((item) => {
        let data = {
          attributeArr: item,
          price: '',
          imgUrl: '',
          originalPrice: '',
        }
        return data
      })
    },
    // 验证规格名
    _validAttributeKey (keyName, KeyNameArr) {
      if (!keyName) {
        this.$message.error('不能添加空规格名称')
        return false
      }
      console.log('KeyNameArr', KeyNameArr)
      let repeatValue = KeyNameArr.filter((item) => {
        return item.keyName === keyName
      })
      if (repeatValue.length) {
        console.log(repeatValue.length)
        this.$message.error('规格名不能重复')
        return false
      }
      return true
    },
    // 上传图片前的处理
    async _uploadSectionFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG/PNG 格式!')
        return false
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 10MB!')
        return false
      }
      const formData = await this.$refs.imageCompressorCmp._imageCompressor(params.file)
      return formData
    },

    // api----------------------------------------------------------------------------------------
    // 上传视频
    _uploa(formData) {
      console.log('formData', formData)
      this.$fileHttp.post('/common/videoUpload/createUploadVideoAdmin', formData).then(res => {
        this.model.videoImageUrl = res.data.data.videoImageUrl
        this.model.videoUrl = res.data.data.videoUrl
        this.model.videoId = res.data.data.id
      })
    },
    // 上传图片
    _upload (formData) {
      return this.$http.post('/common/oss/uploadFile', formData).then(res => res.data.data.webUrl)
    },
    // 添加图片
    _addAdItem() {
      const DATA = this.model
      return this.$http
        .post('/woho/shop/advertising/insert', DATA)
        .then(res => {
          return true
        })
    },
  }
}

export {
  MIXINS_DESCRIBES_APPEND_H5
}
